.Quotes {
  display: grid;
  place-items: center;
}

.Quotes > .slider {
  height:100%;
  max-height: 200px;
  min-height: 100px;
}

.Quotes > .slider .animateOut, .Quotes > .slider .next {
  opacity: 0;
}
.Quotes > .slider .animateIn {
  opacity: 1;
}
.Quotes > .slideCounters {
  display: grid;
  grid-auto-flow: column;
  max-width: 300px;
  width:100%;
  place-content: space-between;
}
.Quotes > .slideCounters > div {
  width:1vmin;
  height:1vmin;
  background-color:var(--gray);
  border-radius: 100%;
  transition: background-color 0.3s ease-out;

}

.Quotes > .slideCounters > div.active {
  background-color: var(--dark-blue);
}


.track > .slide {
  margin:0 auto;
  width:100%;
  height:100%;
  display: grid;
  place-items: center;
}
.track > .slide > div {
  width: 80%;
  font-style: italic;
}
#newsletter-wrapper {
  display: grid;
  place-content: center;
  place-items: center;
  background-color: var(--yellow);
  padding-bottom: 5vh;
}

#newsletter > input[name="email"] {
  width:20vw;
  text-align: center;
}
#newsletter > input {
  font-size: inherit;
  font-family: inherit;
}