.title-wrapper {
  display: grid;
  width:100%;
  height:50vw;
  place-items: center;
  font-size: 12.5vmin;
  background-size: cover;
  /* background-attachment: fixed; */
  height: 40vh;
  color:var(--dark-blue);
  margin-bottom: 2vh;
  border-radius: 10px;
}

.img-text-wrapper {
  display: grid;
  place-items: center;
}

.img-text-wrapper > img {
  width:95%;
  border-radius: 5px;
}

.content {
  width:100%;
}

h2 {
  margin-top:3ch;
  margin-bottom: 0vh;
}

p {
  padding:0vh 3vw;
  text-align: justify;
}

h2 + *, h3 + * {
  margin-top:0;
}

h3 {
  margin-bottom: 0.6rem;
  margin-top: 0.3rem;
  font-size: 1.1rem;
}

hr {
  width:15rem;
  border-color: var(--pastel-blue);
  border-style: solid;
  background: var(--pastel-blue);
}



@media screen and (min-width: 768px) {
  .title-wrapper {
    height:70vh;
  }

  .img-text-wrapper {
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr;
  }

  p {
    padding:0;
  }
}

