.AltTitleOuter {
  display: grid;
  place-items: center;
  /* place-content: center; */
  overflow: hidden;
}

.AltTitleInner {
  display: grid;
  grid-auto-flow: column;
  /* place-content: center; */
  place-items: center;
  gap:2vh;
  grid-auto-rows: 1fr;
  grid-auto-columns: 0.5fr 1fr;
  height:20vh;
}

.AltTitleInner > .yellow-box {
  background-color: var(--yellow);
  width:200%;
  min-height: 50%;
  transform: translateX(7vw);
  animation-name: movRight;
}
.AltTitleInner > .content {
  font-size: 12.5vmin;
  transform: translateX(-7vw);
  font-family: "Advent Pro";
  animation-name: movLeft;
}

.AltTitleInner > .yellow-box, .AltTitleInner > .content {
  animation-timing-function: ease-out;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
}

@media screen and (min-width: 768px) { 
  .AltTitleOuter {
    width:100%;
    place-items:self-end;
  }
  .AltTitleInner {
    grid-auto-columns: 0.7fr 1fr;
  }
  .AltTitleInner > .yellow-box {
    transform: unset;
    min-width: 10vw;
    width:100%;
    animation-name: movRightLarge;
  }
  .AltTitleInner > .content {
    transform: unset;
    font-size: 4rem;
    animation-name: movLeftLarge;
  }

}

@keyframes movRight {
  from {
    opacity: 0;
    transform:translateX(-100vw);
  }
  to {
    opacity: 1;
    transform: translateX(7vw);
  }
}
@keyframes movLeft {
  from {
    opacity: 0;
    transform:translateX(100vw);
  }
  to {
    opacity: 1;
    transform: translateX(-7vw);
  }
}

@keyframes movRightLarge {
  from {
    opacity: 0;
    transform:translateX(-20vw);
  }
  to {
    opacity: 1;
    transform: translateX(0vw);
  }
}
@keyframes movLeftLarge {
  from {
    opacity: 0;
    transform:translateX(20vw);
  }
  to {
    opacity: 1;
    transform: translateX(0vw);
  }
}